.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-BG {
  background: radial-gradient(circle at 15% 50%, #d4cffaa6 10%, transparent 30%),
    radial-gradient(circle at 50% 30%, #d4cffadd 10%, transparent 30%),
    radial-gradient(circle at 85% 65%, #fef6e4 2%, transparent 30%);

  height: auto;

  @media screen and (max-width: 768px) {
    background: radial-gradient(
        circle at 15% 50%,
        #d4cffaa6 5%,
        transparent 30%
      ),
      radial-gradient(circle at 50% 20%, #d4cffadd 5%, transparent 30%),
      radial-gradient(circle at 85% 65%, #fef6e4 2%, transparent 30%);
  }
}
